#time-bar {
    position: absolute;
    bottom: 87px;
    background-color: black;
    color: white;
    width: 100%;
    white-space: nowrap;

    > div {
        float: left;
        width: 50%;
        padding: 0 20px;
        box-sizing: border-box;
        text-align: right;

        > div {
            display: inline-block;
            text-align: center;
            border-left: 1px solid gray;
            border-right: 1px solid gray;
            padding: 5px;
            height: 100%;
            box-sizing: border-box;
            cursor: pointer;
        }

        .time {
            color: @accentColor;
            font-weight: bold;
            font-family: monospace;
        }
    }

    .left > div:nth-child(2) {
        margin-right: 15%;
    }

    .left > div:last-child {
        margin-right: 20%;
    }

    .right > div:nth-child(3) {
        margin-left: calc(~"50% - 208px");
    }

    .right > div:nth-child(4) {
        margin-right: calc(~"45% - 172px");
    }

    img {
        margin-top: 2px;
    }
}

@media screen and (max-width: 1000px) {
    #time-bar {
        img {
            display: none;
        }

        > div {
            padding: 0 6px;

            > div {
                padding: 2px;
            }
        }

        .left > div, .right > div {
            margin: 0 !important;
            display: block;
            float: left;

            + div {
                border-left: none;
            }
        }

        .left > div {
            width: 25%;
        }

        .right > div {
            width: 16%;
        }

        bottom: 55px;
        font-size: 0.45em;
    }
}