#tool-tray {
    position: fixed;
    z-index: 1000;
    bottom: 0px;
    width: 100%;
    height: 87px;
    line-height: 87px;
    background-color: @accentColor3;
    text-align: center;

    .tool {
        display: inline-block;
        vertical-align: middle;
        position: relative;
        width: 127px;
        height: 100%;
        margin: 0 5px;
        max-width: 14%;

        > :first-child {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            * {
                width: 100%;
                height: 100%;
            }
        }

        > :last-child {
            position: relative;
            width: 100%;
            vertical-align: middle;
            max-width: 127px;
            -ms-pointer-events: none;
            pointer-events: none;
        }
    }
}

@media screen and (max-width: 1000px) {
    #tool-tray {
        height: 55px;
        line-height: 55px;

        .tool {
            margin: 0;
        }
    }
}