@import 'common';
@import 'game';

html, body {
    height: 100%;
    margin: 0;
    overflow: hidden;
    font-family: Arial, Helvetica, sans-serif;
    touch-action: none;
    -ms-touch-action: none;
}

body {
    background: url(../Images/slide-background.png);
    background-size: cover;
    -ms-content-zooming: none;
}

// Pulsing dot animation:

.pulse {
    width: 20px;
    height: 20px;
    background-color: @accentColor3;
    border-radius: 10px;
    margin: -10px;
    position: absolute;
    animation-name: pulse;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

@keyframes pulse {
    from {
        box-shadow: 0 0 3px 0 #333;
    }
    50% {
        box-shadow: 0 0 15px 10px @accentColor3;
    }
    to {
        box-shadow: 0 0 3px 0 #333;
    }
}

#video-container {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 2000;

    video {
        position: absolute;
        top: 20px;
        left: 20px;
        right: 20px;
        bottom: 0;
        margin: 0 auto;
        width: 80%;
        max-width: 1000px;
        max-height: 400px;
    }
}

.remediation-icon {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 0;
    margin: 0 auto;
    z-index: 3000;
    width: calc(~"100% - 40px");
    height: calc(~"100% - 220px");
    display: none;
}

.carton {
    position: absolute;
    top: -53px;
    left: 220px;
    height: 53px;
    width: 170px;
    text-align: center;
    background-image: url('../Images/Game/carton-background.png');
}

.order {
    position: absolute;
    top: 0;
    left: -10000px;
    bottom: 220px;
    margin: auto 0;
}

// Quiz

#question-container {
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 2000;

    #question {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;

        background-color: @accentColor2;
        border: 2px solid @accentColor3;
        box-sizing: border-box;
        max-width: 800px;
        max-height: 420px;
        overflow-y: auto;
        padding: 20px;
        width: 100%;
        height: 100%;
        color: white;
        font-weight: bold;
    }

    #question-text {
        margin-bottom: 1.25em;
    }

    #answers {
        list-style: upper-alpha;
        list-style-position: inside;
        padding-left: 0;
        -moz-user-select: none;
        -ms-user-select: none;
        -webkit-user-select: none;
        user-select: none;

        &.right li.right, &.wrong li.right {
            background-color: #33A907 !important;
        }

        &.wrong li.selected {
            background-color: #666 !important;
            opacity: 0.75;
        }

        .answer {
            margin-bottom: 0.75em;
            padding: 10px;
            background-color: rgba(0,0,0, 0.3);
            cursor: pointer;
            padding-left: 2em;
            text-indent: -1.25em;

            &:hover {
                background-color: rgba(0,0,0, 0.2);
            }
        }
    }
}