@smallMargin: 3px;

@backgroundColor: black;
@primaryColor: #424242;

@accentColor:  #E51837;
@accentColor2: #B20738;
@accentColor3: #EAAE30;

@fontColor: black;
@accentFont: black;
@accent2Font: white;

@xlargeFont: 32px;
@largeFont: 22px;
@mediumFont: 16px;
@smallFont: 12px;

@fontFamily: "Arial", "Helvetica", "sans-serif";

// Custom:

.hover-button {
	cursor: pointer;

	.hover {
		display: none;
	}

	&:hover {
		.hover {
			display: block;
		}

		:not(.hover) {
			display: none;
		}
	}
}

// Shepherd
.shepherd-content header {
	float: right;

	.shepherd-cancel-link {
		color: white !important;
	}
}

