@import 'Game/timers';
@import 'Game/animations';
@import 'Game/tooltray';

#container {
    height: calc(~"100% - 100px");
}

#scoreboard {
    position: absolute;
    top: -4px;
    left: 10px;
    height: 52px;
    width: 210px;
    line-height: 55px;
    text-align: center;
    background-image: url('../Images/Game/scoreboard-background.png');
    cursor: default;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    font-weight: bold;
}

#progress {
    position: absolute;
    top: -4px;
    right: 10px;
    height: 52px;
    width: 210px;
    line-height: 55px;
    text-align: center;
    background-image: url('../Images/Game/scoreboard-background.png');
    cursor: default;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    font-weight: bold;
}

@media screen and (max-width: 1000px) {
    #container {
        height: calc(~"100% - 70px");
    }
}